// 一部初期化
img{
    max-width: 100%;
    height: auto;
    image-rendering: crisp-edges;
}
ul,li{
    list-style: none;
    margin-left: 0;
    padding-left: 0;
}
a{
    text-decoration: none;
    color: inherit;
    &:hover{
        color: inherit;
    }
}


// フォント連ねる カンマ区切り
// RcPMinchoは割愛(埋め込み指定&フォントデータ壊れてたため)

@font-face {
    font-family: "Colfax";
    src: url("../public/fonts/Colfax-Regular.otf");
}

@font-face {
    font-family: "SourCeHanSerif-Bold";
    src: url("../public/fonts/SourceHanSerif-Bold.otf");
}
@font-face {
    font-family: "SourCeHanSerif-Regular";
    src: url("../public/fonts/SourceHanSerif-Regular.ttc");

}
@font-face {
    font-family: "SourCeHanSerif-Medium";
    src: url("../public/fonts/SourceHanSerif-Medium.ttf");
}

$font-faces: (
    "txt-en": "Colfax",
    "nav": "Colfax",
    "txt-stmt": "SourCeHanSerif-Medium",
    "txt-title": "SourCeHanSerif-Bold",
    "txt-body": "SourCeHanSerif-Regular"
);

$font-sizes: (
    "fuga": 1px,
    "hogehoge": 1px,
    "piyo": 1px,
);

$breakpoints: (
    "sm": 568px,
    "md": 1030px,
);

// ブレイクポイント記述省略
@mixin min-width($bp){
    @media screen and (min-width: $bp) {
        @content;
    }
}

@mixin max-width($bp){
    @media screen and (max-width: $bp) {
        @content;
    }
}


*{ font-feature-settings: "plat" 1; }

// テキスト デフォ値
p{ font-family: map-get($font-faces , "txt-body"); }
// ユーリティティclass
.block-title{
    font-family: map-get($font-faces, "txt-en");
    font-size: 26px;
    margin-bottom: 12px;
    font-weight: normal;
    @include min-width(map-get($breakpoints, "md")){
        font-size: 32px;
    }
}

@include min-width(map-get($breakpoints, "md")){
    .sp{ display: none !important; }
}
@include max-width(map-get($breakpoints, "md")){
    .pc{ display: none !important; }
}


.link{ text-decoration: underline; }


$mb-box-default:235px;
@media screen and (min-width:680px) {  $mb-box-default:318px; }
.box{ margin-bottom: $mb-box-default; }

// 全体レイアウト共通
$container-width:960px;
$padding-x: 30px;
.container{
    position: relative;
    max-width: calc($container-width + (2*$padding-x));
    margin: 0 auto;
    padding: 10px $padding-x;
    @include max-width(map-get($breakpoints, "md")){
        max-width: 720px;
    }
}

#home-splash{
    display: flex;
    width: 100%;
    height: 100vh;
    margin-bottom: 80px;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    .wrapper-img{
        img{ width: 65%;
            display: block;
             margin: 0 auto;}
        @media screen and (min-width: 680px) {
            img{width: 70%;}
        }
    }
}

#home-stmt{
    pointer-events: none;
    user-select: none;
    margin-top: 120px;

    h2{ 
        font-family: map-get($font-faces, "txt-stmt");
        font-weight: normal;
        margin-bottom: 34px;
        width: 100%;
        font-size: 56px;
        letter-spacing: 3px;

        // フォントサイズ 細かく調整
        @include max-width("380px"){
            font-size: 9.5vw !important;
        }
        @include max-width("450px"){
            font-size: 10.2vw;
        }

        @include min-width(map-get($breakpoints, "md")){
            font-size: 46px;
            margin-bottom: 36px;
            max-width: 703px;
        }
     }

    // 本文の行
    .stmt-line{ 
        font-family: map-get($font-faces, "txt-stmt");
        font-size: 16px;
        line-height: 60.5px; 

        // フォントサイズ 細かく調整
        @include max-width("380px"){
            font-size: 4.5vw !important;
        }
        @include max-width(map-get($breakpoints, "sm")){
            font-size: 4.7vw;
        }

        @include min-width(map-get($breakpoints, "md")){
            font-size: 18px;
            line-height: 41.4px; 
        }
    }
} 

#home-service {
    .service-row {
        display: flex;
        justify-content: space-between;
        margin-bottom: 80px;

        &.reverse {
            flex-direction: row-reverse;
        }

        &:last-of-type {
            margin-bottom: 0;
        }

        .service-col {
            width: 48%;
            font-family: 'SourCeHanSerif-Regular';

            .giftful-logo {
                width: 180px;
            }

            .service-text {
                font-size: 16px;
                line-height: 2;

                &:last-of-type {
                    margin-bottom: 46px;
                }
            }

            @include max-width(map-get($breakpoints, "md")){
                width: 100%;

                .custom-button-wrapper {
                    text-align: center;
                }
            }
        }

        @include max-width(map-get($breakpoints, "md")){
            display: block;
        }
    }
}

#home-information{
    font-size: 19.5px;
    line-height: 37px;
    @include min-width(map-get($breakpoints, "md")){
        font-size: 18px;
    }
    
}

// 紹介リンクのアイコン基礎クラス
@mixin icon-common {
    &::before{
        content: "";
        display: block;
        width: 25px;
        height: 100%;
        background-size: contain;
        background-position-y: center;
        background-repeat: no-repeat;
        @include min-width(map-get($breakpoints, "md")){
            width: 22px;            
        }
        @content;
    }
}

#home-member{
    $box-size-member: 270px;
    $mb-box-member: 250px;
    $sp-line-h: 37px;
    margin-bottom: calc($mb-box-member - 110px);
    .wrapper-members{
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start; 
        @include min-width(map-get($breakpoints, "md")){
            gap: 70px;
            // max-width: calc(70px + (2*$box-size-member));
        }
    }
    .member, .dummy{
        width: 100%;
        @include min-width(map-get($breakpoints, "md")){
            max-width: $box-size-member;
        }
    }
    .dummy{ height: 0; }
    .member{ 
        margin-bottom: 60px;
        @include min-width(map-get($breakpoints, "md")){
            margin-bottom: 110px;
        }
     }
    // 紹介
    .wrapper-name{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        
        font-size: 18px;
        line-height: $sp-line-h;
        font-weight: normal;
        font-family: map-get($font-faces, "txt-title");
        img{ vertical-align: text-bottom; }
        @include min-width(map-get($breakpoints, "md")){
            font-size: 14px;
            line-height: 32px;
        }
    }
    .body{
        font-size: 18px;
        line-height: $sp-line-h;
        text-align: justify;
        font-family: map-get($font-faces, "txt-body");
        @include min-width(map-get($breakpoints, "md")){
            font-size: 13.5px;
            line-height: 26px;
        }
    }
    // リンクアイコン 速さ優先の悪手
    .i-twitter{
        @include icon-common{
            background-image: url("../public/img/i-twitter.png");}
        &:hover::before{
            background-image: url("../public/img/i-twitter-bk.png");}
        }
    .i-de{
        @include icon-common{
            background-image: url("../public/img/i-de.png");}
        &:hover::before{
            background-image: url("../public/img/i-de-bk.png");}
        }
}

.fontH3 {
    font-size: 18px;
    line-height: 2;
    font-weight: 500;
}

.g-nav-hide{
    pointer-events: none;
}
.g-nav-show{
    pointer-events: unset;
}
// グローバルナビ z:100基準
#g-nav{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 120;
    width: 100%;
    // max-width: 1500px;

    height: 65px;
    background-color: #fff;
    font-family: map-get($font-faces, "nav");

    // SP非表示 開閉動作は最下部
    pointer-events: none;
    @include min-width(map-get($breakpoints, "md")){
        pointer-events: unset;
    }
    .wrapper{
        display: flex;
        width: 100%;
        // max-width: 1350px;
        height: 100%;
        padding: 0 20px;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
        margin: 0 auto;

        // max-width: $container-width;
    }
    nav {
        display: none;
        a{ 
          font-size: 20px;
          letter-spacing: 1.5px;
           margin-right: 30px;
           &:nth-last-of-type(1){ margin-right: 0; }
        }
        @include min-width(map-get($breakpoints, "md")){
            display: unset;
        }
    }
    .logo{
        margin-top: 10px;
        height: 48px;
        @include min-width(map-get($breakpoints, "md")){
            height: 54px;
        }
    }
}
#nav-drower{
    position: fixed;
    z-index: 110;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;

    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    background-color: #fff;

    font-size: 40px;
    line-height: 87px;
    font-family: map-get($font-faces, "nav");

    opacity: 0;
    pointer-events: none;
    transition: .3s ease;
    @include min-width(map-get($breakpoints, "md")){
        display: none !important;
    }
}
#nav-drower.active{
    opacity: 1;
    pointer-events: unset;
    transition: .3s ease;
}




footer{
    font-family: map-get($font-faces, "nav");
    margin-bottom: 10px;
    .wrapper{
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }

}
#jump-to-top{
    position: absolute;
    right: 20px;
}


// ハンバーガとgnav

#btn-hamburger{
    position: fixed;
    display: block;
    right: 20px;
    top: 0;
    width: 25px;
    height: 65px;
    cursor: pointer;
    z-index: 125;
    .wrappar{
        position: relative;
        height: 25px;
    }    
    .hamburger_line {
        position: absolute;
        left: 0;
        width: 25px;
        height: 1px;
        background-color: #000;
        -webkit-transition: all 0.6s;
        transition: all 0.6s;
    }
    .hamburger_line_1{ top: 5px; }
    .hamburger_line_2{ top: 20px; }
    @include min-width(map-get($breakpoints, "md")){
        display: none !important;
    }
} 



.active{
    .hamburger_line_1 {
        -webkit-transform: rotate(25deg);
        transform: rotate(25deg);
    }
        
    .hamburger_line_2 {
        -webkit-transform: rotate(-25deg);
        transform: rotate(-25deg);
        top: 5px !important;

    }
}

#custom-button {
    border: 2px solid #000;
    border-radius: 50px;
    padding: 12px 20px;
    min-width: 200px;
    font-size: 14px;
    line-height: 2;
}


// めちゃくちゃ急いで作り直した産物 機会があれば直したい...
#g-nav.active{
    opacity: 1;
    pointer-events: unset;
    background-color: unset;
}
